import styled from "@emotion/styled";

export const Wrapper = styled.div`
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 150px;
  p {
    /* visibility: hidden; */
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, 50%);
    color: white;
  }
`;
