import { Contacts } from "../../pages";
import { Wrapper, Nav, NavigateLink } from "./Navigation.styled";

export const Navigation = ({ onClose }) => {
  return (
    <Nav>
      <Wrapper>
        <li>
          <NavigateLink to="/projects" onClick={onClose}>
            
          </NavigateLink>
        </li>
        <li>
          <Contacts />
        </li>
        {/* <li>
          <NavigateLink to="/contacts" onClick={onClose}>
            
          </NavigateLink>
        </li> */}
      </Wrapper>
    </Nav>
  );
};
