import { Carousel } from "antd";
import "antd/dist/antd.min.css";
// import { PictureItem } from "../PictureItem/PictureItem";

const Slider = ({ data }) => {
  return (
    <Carousel
      autoplay
      effect="fade"
      easing="linear"
      touchMove="true"
      style={{
        display: "flex",
        overflow: "hidden",
        height: "500px",
        alignItems: "center",
        width: "100%",
        padding: "3px",
      }}
    >
      {data.map((d, id) => (
        <img src={d.photo} alt="project" key={id} width="100%" />
      ))}
    </Carousel>
  );
};
export default Slider;
